#navbar {
  border: 2px solid black;
  display: flex;
  justify-content: flex-end;
  margin-bottom: 5em;
  background-color: burlywood;
  background-color: rgb(134, 125, 103);
}
button {
  padding: 1.25em;
  border-radius: 10%;
  background-color: rgb(97, 126, 150);
}
button:hover {
  cursor: pointer;
  opacity: 0.6;
  box-shadow: 1px 2px 3px 4px red inset;
}
#abc {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  display: grid;
  grid-template-columns: auto auto auto auto;
  gap: 50px;
}
.loading {
  height: 4em;
  width: 4em;
  border-radius: 50%;
  border-top: 2px solid black;
  border-left: 2px solid black;
  animation-name: goods;
  animation-duration: 2s;
  animation-timing-function: ease-in;
  animation-iteration-count: infinite;
}
#loadingcover {
  display: flex;
  justify-content: center;
}
@keyframes goods {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
body {
  height: 100vh;
  background-color: rgb(152, 211, 94);
}
.ramji {
  /* border:1px solid rgb(168, 126, 126); */
  padding: 1em;
}
.sitaji {
  border: 1px solid rgb(131, 81, 81);
}
.sitaji:hover {
  cursor: pointer;
  opacity: 0.9;
}
